<template>
<div class="container">
    <div class="row">
        <div class="col-md-10 offset-md-1">
            <div class="alert alert-light" role="alert">
                <h4 class="alert-heading"> LEEPs Bureau De Change</h4>
                <p v-bind="{date}">Our Current Exchange Rates  {{` ${date}`}}</p>
                
<table class="table table-striped">
  <thead>
    <tr>
      <th scope='col'> S/N</th>
      <th scope="col">Currency</th>
      <th scope="col">Value</th>
      
    </tr>
  </thead>
  <tbody>
    <tr v-for="(items, key, index) in rateValues" :key="index"> 
      <td>{{index + 1}}</td>
      <td > {{key}}</td>
      <td>{{items}}</td>
    </tr></tbody></table>
                <p class="mb-0" v-bind="{base}"> Our Base Currency {{base}}.</p>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import axios from 'axios';
export default {
    name: "leeps",
    data(){
        return {
            rateValues: null,
            base: null,
            date:null,
        }
    },
    mounted: function(){
        axios.get(' https://api.exchangeratesapi.io/latest')
        .then(response =>{ 
            this.values=response.data;
             this.rateValues =this.values.rates;
            this.base =  this.values.base;
            this.date = this.values.date;
        });
    }
}
</script>

